import React from 'react';
import './App.css';
import Searchbar from './components/Searchbar';
import Card from './components/Card';
import Header from './components/Header';
import Splash from './components/Splash';




class App extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      ArrayVA: []
    }
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handleSubmit(e){
    e.preventDefault();
    let searchBox = document.querySelector("#search-box");
    let searchValue = searchBox.value;
    searchBox.blur();
  
    fetch("https://api.soundcloud.com/tracks?q=" + searchValue +"&client_id=PGBAyVqBYXvDBjeaz3kSsHAMnr1fndq1")
      .then(response => response.json())
        .then(data => {
          console.log(data)
            this.setState({
              ArrayVA: data.map((item) => (
                <Card 
                  title={item.title}
                  artwork={item.artwork_url}
                  link={item.permalink_url}
                />
              ))
            })
        console.log()
    })
  }
  componentDidMount(){
    // alert('@Mounted')
    setTimeout(function(){
      document.getElementById("splash-container").style.display = "none";
    }, 5300);
  }
  render(){
    return (
    <div className="App">

      <Header />
      <Searchbar placeholder="Search..." handleSubmit={this.handleSubmit} />

      <div className="cards-container">{this.state.ArrayVA}</div>


      <Splash />
    </div>
    )
  }
}






export default App;
